.fullBlueprint {
    position: relative;
    width: fit-content;
}

.exportableClass {
    width: calc(2174px * 2);
    height: calc(1248px * 2);
}

.fullSize {
    transform-origin: top left;
    scale: 2;
}

.previewSize {
    transform-origin: top left;
    scale: 0.9;
}

.rosterGraphic {
    position: absolute;
    top: 285px;
    left: 50px;
    transform-origin: top left;
    scale: 0.88;
}

.settingsGraphic {
    position: absolute;
    top: 124px;
    left: 39px;

    transform-origin: top left;
    scale: 0.884;
}

.cornerstonesGraphic {
    position: absolute;
    bottom: 80px;
    left: 410px;
    transform-origin: bottom left;
    scale: 0.4;
}

.risersFallersGraphic {
    position: absolute;
    bottom: 415px;
    left: 75px;
}

.suggestedMovesGraphic {
    position: absolute;
    bottom: 55px;
    left: 910px;
    scale: 0.44;
    transform-origin: bottom left;
}

.holdsGraphic {
    position: absolute;
    scale: 0.44;
    transform-origin: bottom left;
    bottom: 153.5px;
    left: 1870px;
}


.positionalGradesGraphic {
    position: absolute;
    scale: 0.59;
    transform-origin: bottom left;
    bottom: 15px;
    left: 40px;
}

.teamName {
    position: absolute;
    top: 15px;
    left: 60px;
    font-family: 'Cheddar Gothic Rough';
    font-size: 50px;
    color: white;
}

.bpCode {
    position: absolute;
    width: 360px;
    text-align: center;
    color: #D25354;
    font-family: 'Erbaum Medium';
    font-size: 40px;
    top: 52px;
    left: 1783px;
}

.threeYearOutlook {
    position: absolute;
    transform-origin: top left;
    scale: 0.25;
    top: 591px;
    left: 495.4px;
}

.graphCrop {
    width: 1250px;
    overflow: hidden;
}

.graph {
    margin: 0px 0px 0px -10px;
}

.dvm {
    position: absolute;
    transform-origin: top left;
    scale: 0.25;
    top: 52px;
    left: 950px;
}

.archetypeGraphLabel {
    position: absolute;
    font-family: 'Erbaum Medium';
    width: 250px;
    text-align: center;
    rotate: -90deg;
    top: 690px;
    left: 290px;
}

.otherSettings {
    position: absolute;
    color: white;
    font-family: 'Erbaum Medium';
    font-size: 22px;
    top: 188px;
    left: 150px;
}

.rookiePickHeader1, 
.rookiePickHeader2 {
    position: absolute;
    color: white;
    font-family: 'Erbaum Black';
    width: 350px;
    height: 50px;
    text-align: center;
    left: 1790px;
}

.rookiePickHeader1 {
    top: 645px;
}
.rookiePickHeader2 {
    top: 760px;
}

.rookiePickComment1,
.rookiePickComment2 {
    position: absolute;
    color: white;
    font-family: 'Erbaum';
    width: 350px;
    height: 50px;
    text-align: center;
    left: 1790px;
}

.rookiePickComment1 {
    top: 675px;
}

.rookiePickComment2 {
    top: 790px;
}

.suggestionsAndComments {
    position: absolute;
    color: white;
    font-family: 'Acumin Pro';
    font-size: 16px;
    width: 365px;
    height: 380px;
    top: 200px;
    left: 1775px;
}



.offScreen {
    position: absolute;
    left: -9999px;
    top: -9999px;
    margin: 30px;
}

.preview {
    transform-origin: top left;
    scale: 0.3;
}
